body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.cityDetails {
  max-width: 800px;
  margin: 30px auto;
  border-radius: 15px;
  padding: 30px;
  border: 1px solid rgba(250,250,250, .8);
}
.cityDetails.red {
  background-color: rgba(255,0,0, .6);
}
.cityDetails.yellow {
  background-color: rgba(255,255,0, .6);
}
.cityDetails.green {
  background-color: rgba(50,205,50, .6);
}
.cityDetails .pm25 {
  text-transform: uppercase;
  line-height: 60px;
}
.cityDetails .pm25 span {
  font-size: 60px;
  line-height: inherit;
}
p span {
  font-weight: bold;
}


